import React, { useState, useEffect } from 'react';
import { BarChart3, Clock, CheckCircle, AlertCircle, TrendingUp, History } from 'lucide-react';
import { taskAPI } from '../services/api';
import { TaskHistory } from '../types';

const Analytics: React.FC = () => {
  const [analytics, setAnalytics] = useState<any>(null);
  const [history, setHistory] = useState<TaskHistory[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchAnalytics();
    fetchHistory();
  }, []);

  const fetchAnalytics = async () => {
    try {
      const data = await taskAPI.getAnalytics();
      setAnalytics(data);
    } catch (error) {
      // Analytics failure shouldn't break the app
      setAnalytics({ avg: 0, trend: [] });
    }
  };

  const fetchHistory = async () => {
    try {
      const data = await taskAPI.getTaskHistory();
      setHistory(data.slice(0, 5)); // Show last 5 activities
    } catch (error) {
      // History failure shouldn't break the app
      setHistory([]);
    } finally {
      setLoading(false);
    }
  };


  if (loading) {
    return (
      <div className="space-y-6">
        <div className="animate-pulse">
          <div className="bg-white dark:bg-gray-800 p-4 rounded-lg">
            <div className="h-4 bg-gray-200 dark:bg-gray-700 rounded w-3/4 mb-3"></div>
            <div className="h-8 bg-gray-200 dark:bg-gray-700 rounded"></div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Analytics Overview */}
      <div className="bg-white dark:bg-gray-800 rounded-lg p-6 shadow-sm">
        <div className="flex items-center gap-2 mb-4">
          <BarChart3 className="h-5 w-5 text-blue-500" />
          <h3 className="font-semibold text-gray-900 dark:text-white">
            Analytics Overview
          </h3>
        </div>

        {analytics && (
          <div className="space-y-4">
            <div className="flex items-center justify-between">
              <span className="text-gray-600 dark:text-gray-400 flex items-center gap-2">
                <Clock className="h-4 w-4" />
                Avg. Completion Time
              </span>
              <span className="font-medium text-gray-900 dark:text-white">
                {analytics.avg}min
              </span>
            </div>

            {analytics.trend && analytics.trend.length > 0 && (
              <div>
                <div className="text-sm text-gray-600 dark:text-gray-400 mb-2 flex items-center gap-1">
                  <TrendingUp className="h-4 w-4" />
                  Productivity Trend (Sliding Window)
                </div>
                <div className="flex items-end gap-1 h-16">
                  {analytics.trend.map((value: number, index: number) => (
                    <div
                      key={index}
                      className="bg-blue-500 rounded-t flex-1 min-w-0"
                      style={{
                        height: `${Math.max(4, (value / Math.max(...analytics.trend)) * 100)}%`
                      }}
                      title={`Day ${index + 1}: ${value}min`}
                    />
                  ))}
                </div>
              </div>
            )}
          </div>
        )}
      </div>

      {/* Task Summary Stats */}
      <div className="grid grid-cols-2 gap-4">
        <div className="bg-white dark:bg-gray-800 rounded-lg p-4 shadow-sm">
          <div className="flex items-center gap-2 mb-2">
            <CheckCircle className="h-4 w-4 text-green-500" />
            <span className="text-sm text-gray-600 dark:text-gray-400">Completed</span>
          </div>
          <div className="text-2xl font-bold text-green-600 dark:text-green-400">
            {analytics?.completedTasks || 0}
          </div>
        </div>

        <div className="bg-white dark:bg-gray-800 rounded-lg p-4 shadow-sm">
          <div className="flex items-center gap-2 mb-2">
            <AlertCircle className="h-4 w-4 text-orange-500" />
            <span className="text-sm text-gray-600 dark:text-gray-400">Pending</span>
          </div>
          <div className="text-2xl font-bold text-orange-600 dark:text-orange-400">
            {analytics?.pendingTasks || 0}
          </div>
        </div>
      </div>

      {/* Recent Activity (Linked List History) */}
      <div className="bg-white dark:bg-gray-800 rounded-lg p-6 shadow-sm">
        <div className="flex items-center gap-2 mb-4">
          <History className="h-5 w-5 text-purple-500" />
          <h3 className="font-semibold text-gray-900 dark:text-white">
            Recent Activity
          </h3>
          <span className="text-xs text-gray-500 dark:text-gray-400">(Linked List)</span>
        </div>

        <div className="space-y-3 max-h-64 overflow-y-auto">
          {history.length > 0 ? (
            history.map((item, index) => {
              const getActionColor = (action: string) => {
                switch (action.toLowerCase()) {
                  case 'created': return 'bg-green-500';
                  case 'updated': return 'bg-blue-500';  
                  case 'deleted': return 'bg-red-500';
                  case 'completed': return 'bg-purple-500';
                  default: return 'bg-gray-500';
                }
              };
              
              const getActionIcon = (action: string) => {
                switch (action.toLowerCase()) {
                  case 'created': return '+';
                  case 'updated': return '';
                  case 'deleted': return '';
                  case 'completed': return '';
                  default: return '';
                }
              };

              return (
                <div key={index} className="flex items-start gap-3 text-sm">
                  <div className={`w-6 h-6 ${getActionColor(item.action)} rounded-full flex items-center justify-center text-white text-xs font-bold flex-shrink-0`}>
                    {getActionIcon(item.action)}
                  </div>
                  <div className="flex-1 min-w-0">
                    <span className="text-gray-900 dark:text-white capitalize">
                      Task {item.action.toLowerCase()}
                    </span>
                    <div className="text-gray-500 dark:text-gray-400 text-xs">
                      {new Date(item.timestamp).toLocaleTimeString()}  {new Date(item.timestamp).toLocaleDateString()}
                    </div>
                  </div>
                </div>
              );
            })
          ) : (
            <div className="text-center text-gray-500 dark:text-gray-400 py-4">
              No recent activity
            </div>
          )}
        </div>
      </div>

    </div>
  );
};

export default Analytics;